.topbar {
    height: 88px;
    display: flex;
    align-items: center;
    background: #233750;
}

.container {
    width: 100%;
    padding-right: 25px;
    padding-left: 25px;
    margin-right: auto;
    margin-left: auto;
}

.divide {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.divide .dv-left {
    width: 50%;
    justify-content: flex-start;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    align-items: center;
}

.divide .dv-right {
    justify-content: flex-end;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    align-items: center;
}

.search {
    width: 100%;
}

.search-bar {
    width: 86%;
    border-radius: 50px;
    border: 0;
    height: 30px;
    margin-left: 10px;
    padding: 5px 5px 5px 20px;
    margin-top: 8px;
    /* margin-right: 16px; */
    font-size: 17px;
}

.topbar .search .search-icon {
    float: right;
    padding: 3px 6px;
    /* margin-top: 10px; */
    /* margin-right: 16px; */
    background: white;
    font-size: 17px;
    border: none;
    cursor: pointer;
    position: absolute;
    margin-left: -50px;
    border-radius: 50%;
    top: 30px;
    box-shadow: 0 2px 6px 0 rgb(0 0 0 / 20%);
}

.MuiButton-root {
    color: white !important;
}

.dv-right>div {
    z-index: 1;
}

.card {
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1);
    transition: 0.3s;
    width: 100%;
    border-radius: 5px;
    /* max-width: 40%; */
    padding-bottom: 15px;
}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

.card-img{
    position: relative;
    cursor: pointer;
}

.card-img img {
    border-radius: 10px;
    /* max-width: 100%; */
    object-fit: cover;
    display: block;
    width: 100%;
}

.card-img>div>div {
    width: 100%;
}

.card-like {
    display: flex;
    justify-content: center;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    background: white;
    box-shadow: 0 2px 6px 0 rgb(0 0 0 / 16%);
    position: absolute;
    top: 12px;
    right: 12px;
}

.card-store {
    color: #939393;
    margin: 0 1rem ;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.card-store h5 {
    color: #939393;
    font-size: 14px;
    margin: .5rem 0;
}

.card-name {
    margin: 0 1rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.card-name h3 {
    color: black;
    font-size: 1.4rem;
    font-weight: 600;
    margin: .5rem 0;
}

.card-price {
    margin: .5rem 1rem;
    color: #f60;
    font-size:1rem;
    font-weight: 700;
}

.card-price span{
    font-size: .75rem;
    color: #939393;
    font-weight: 600;
    margin: 0 .5rem;
    font-size: 14px
}

.column {
    float: left;
    width: 23.3%;
    padding: 10px;
}

.row {
    padding: 25px;
}
  
.row:after {
    content: "";
    display: table;
    clear: both;
}

.card-rating .MuiBox-root{
    margin-bottom: 0;
    margin-top: 1rem;
}

.card-rating .MuiRating-root {
    font-size: 2rem;
}